:root {
  --grey-dark: #444444;
  --btn-gray-faded: rgb(68, 68, 68, 0.8);
  --btn-gray-faded-strong: rgb(68, 68, 68, 0.6);
  --grey-darker: #292929;
  --grey-light: #818181;
  --white-bone: #cfcfcf;
  --emerald-light: #4394a9;
  --emerald-shine: #5cc9e5;
  --transition-time: 0.4s;
  --shadow-radius: 25px;
  --border-radius-card: 5px;
  --border-radius-card-cat: 10px;
}

/* Text selectin color*/
::selection {
  background: var(--emerald-light); /* WebKit/Blink Browsers */
}
::-moz-selection {
  background: var(--emerald-light); /* Gecko Browsers */
}

* {
  /* user-select: none; */
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  color: #ffffff;
  font-family: "Poppins", sans-serif;
  transition-duration: var(--transition-time);
  -webkit-transition-duration: var(--transition-time); /* Safari */
  line-height: 1.4em;
  letter-spacing: 0.02em;
}

body,
html {
  /* position: relative; */
  /* width: 100vw;
  height: 100vh; */
  overflow-x: hidden;
  background-color: black;
  text-align: center;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

a, button {
  font-weight: 600;
  cursor: pointer;
}

a {
  text-decoration: none;
}

button {
  border: none;
  background-color: transparent;
}

h1 {
  font-size: 4em;
  margin-bottom: 0.25em;
  border-bottom: 4px solid var(--emerald-shine);
}

h3 {
  font-weight: normal;
  font-size: 1.6em;
}

h4 {
  font-size: 1.5em;
}

img {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-select: none;
}

.hero-section-container {
  height: 100%;
}

.hero-image {
  height: 100vh;
  width: 100vw;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  animation: slowFloatDown 1.5s ease;
}

.hero-profile-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.9)),
    url("./images/profileTitle.jpg");
}

.hero-projects-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.8)),
    url("./images/projectsTitle.jpg");
}

.hero-mycat-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.8)),
    url("./images/catTitle.jpg");
}

.hero-text-container {
  user-select: none;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  white-space: nowrap;
}

/* Header / Nav Menu */
.nav-main {
  width: 100%;
  background-color: black;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  height: 10vh;
}

.home-btn:hover {
  color: white;
}

.nav-main a {
  font-size: 1em;
}

.header {
  width: 100%;
  height: inherit;
  background-color: black;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}

.footer {
  bottom: 0;
  margin-top: 5em;
  justify-content: right;
  background-color: var(--grey-darker);
}

.btn-totop {
  font-size: 1.5em;
  width: 2em;
  height: 2em;
  border-radius: 50%;
  background-color: var(--grey-darker);
  color: var(--emerald-shine);
  opacity: 0.6;
  position: fixed;
  right: 2vh;
  bottom: 10vh;
}

.btn-totop-icon {
  filter: invert(48%) sepia(13%) saturate(3207%) hue-rotate(140deg)
  brightness(120%) contrast(90%);
  display: block;
  padding: 0.5em;
  width: inherit;
  height: inherit;
}

.header-content {
  z-index: 15;
}

.btn-media {
  width: 1.7em;
  height: 1.7em;
}

.media-icon {
  display: block;
  width: inherit;
  height: inherit;
  transition-duration: 0s;
}

.nav-header-links {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  z-index: 15;
  transition: transform 0.9s;
}

.nav-header-links a, 
.nav-header-links button{
  margin-left: 2em;
}

.nav-header-links img {
  cursor: pointer;
}

.header-hamburger {
  display: none;
  cursor: pointer;
}

.header-hamburger span {
  display: block;
  margin: 0.4em 0;
  width: 1.5em;
  height: 0.15em;
  background-color: white;
}

/* Sections */
.header-section {
  border-left: 4px solid var(--emerald-shine);
  border-right: 4px solid var(--emerald-shine);
  padding: 0 0.5em;
}

.section {
  margin: 2em 0 2em 0;
  font-size: 2em;
  display: inline-block;
}

.section-sub {
  margin-bottom: 2em;
  font-size: 1.8em;
}

.center-section {
  display: flex;
  justify-content: center;
}

.section-text-container {
  text-align: left;
  margin: 0 5em;
}

.section-text-container h4 {
  margin-bottom: 1em;
}

.section-text-container p {
  margin-bottom: 1.5em;
}

.section-text-content-headline {
  color: var(--emerald-shine);
}

.contactme-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
  margin: 5%;
}

.contactme-container h2 {
  font-size: 1.1em;
}

.contactme-container p {
  margin-bottom: 2.5%;
}

.contacts-email {
  font-size: 1em;
  margin-top: 0.5em;
  color: var(--emerald-shine);
  font-weight: 600;
  cursor: default;
}

/* Projects, Project-Card */
.projects-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: auto;
  column-gap: 1.5em;
  row-gap: 1em;
  text-align: center;
  margin: 0 2em;
}

.project-card {
  border-radius: var(--border-radius-card);
  width: 100%;
  height: 100%;
  box-shadow: 0px 0px 2px 1px var(--emerald-light);
}

.project-card:hover {
  transform: scale(1.03);
  box-shadow: 0px 0px 10px 5px var(--emerald-light);
}

.project-card:hover > .project-card-content {
  transform: translate(-50%, -50%);
  opacity: 1;
  transition-duration: 0.5s;
  visibility: visible;
  pointer-events: auto;
}

.project-card:hover > .project-card-img {
  opacity: 0.15;
}

.project-card-content {
  width: 100%;
  pointer-events: none;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  /* transform: translate(-50%, 50%); */
  transform: translate(-50%, -100%);
  color: white;
  display: flex;
  justify-content: center;
  flex-direction: column;
  visibility: hidden;
  opacity: 0;
}

.project-card-img {
  width: 100%;
  height: 100%;
  border-radius: var(--border-radius-card);
  background: linear-gradient(transparent 75%, var(--emerald-light));
  background: -webkit-linear-gradient(transparent 75%, var(--emerald-light));
}

.project-card-title {
  font-size: 2em;
  text-align: center;
  color: var(--emerald-shine);
}

.project-card-description {
  margin: 2em 1em;
  font-size: 1.2em;
}

.project-card-links-container {
  display: flex;
  justify-content: center;
}

.project-card-link-wrapper {
  margin: 0.8em 0;
}

.project-card-link {
  margin: 0 0.3em;
  font-size: 1.2em;
  border: 0.08em solid white;
  padding: 0.2em;
}

/* Carousel */
.carousel {
  overflow: hidden;
  width: 100%;
  /* touch-action: none; */
}

.carousel-slides {
  white-space: nowrap;
  transition: transform 0.4s;
  width: 100%;
}

/* .carousel-slides::before {
  content: '';
  position: absolute;
  top: 0;
  left: 100%;
  width: 10000%;
  height: 100%;
} */

.carousel-item-container {
  display: inline-flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  width: 100%;
  margin-right: 150%;
}

.carousel-item {
  max-width: 100%;
  height: 50vh;
  border-radius: var(--border-radius-card-cat);
}

.carousel-btn {
  color: white;
  font-size: 1.5rem;
  font-weight: 900;
  width: 2em;
  height: 2em;
  align-self: center;
  background-color: var(--btn-gray-faded);
  border-radius: 50%;
  position: absolute;
  padding: 0.3em;
  margin: 0 15em;
  z-index: 5;
  /* box-sizing: content-box; */
}

.carousel-btn-prev {
  left: 0;
}

.carousel-btn-next {
  right: 0;
}


.carousel-radio-section {
  margin-top: 1em;
}

/* Radio */
input[type="radio"] {
  height: 0.75rem;
  width: 0.75rem;
  background-color: transparent;
  border-radius: 50%;
  margin-right: 1rem;
  position: relative;
  cursor: pointer;
  -webkit-appearance: none;
}

input[type="radio"]:after {
  background-color: var(--grey-light);
  border-radius: 50%;
  content: "";
  display: block;
  height: inherit;
  width: inherit;
  left: 0;
  position: relative;
}
input[type="radio"]:checked:after {
  background-color: var(--grey-darker);
  box-shadow: 0 0 0 1px var(--grey-darker);
}

/* Scrollbar*/
@media screen and (min-width: 1024px) {
  body::-webkit-scrollbar {
    width: 0.5em;
  }

  body::-webkit-scrollbar-track {
    background-color: black;
  }

  body::-webkit-scrollbar-thumb {
    background-color: var(--grey-dark);
    background-clip: padding-box;
    /* outline: 1px solid var(--grey-light); */
  }
}




@media (hover: hover) {
  a:hover {
    color: var(--emerald-shine);
  }

  .btn-totop:hover {
    transform: scale(1.3);
    background-color: var(--grey-dark);
    opacity: 1;
  }

  .media-icon:hover {
    filter: invert(48%) sepia(13%) saturate(3207%) hue-rotate(140deg)
      brightness(100%) contrast(90%);
  }

  .project-card-link:hover {
    color: var(--emerald-shine);
    border-color: var(--emerald-shine);
  }

  .carousel-btn:hover {
    transform: scale(1.2);
    background-color: var(--grey-darker);
    color: var(--emerald-shine);
  }
}





