/* Components Animation*/
@keyframes slowFloatDown {
	0% {
		opacity: 0;
		transform: translateY(-100px);
	}
	100% {
		opacity: 1;
		transform: translateY(0px);
	}
}


/* Hamburger */
.ham-toggle .line1 {
  transform: rotate(45deg) translate(0.3em, 0.7em);
}
.ham-toggle .line2 {
  opacity: 0;
}
.ham-toggle .line3 {
  transform: rotate(-45deg) translate(0.07em, -0.5em);
}
